.wp-block-create-block-starter-block {
  background-color: var(--wp-admin-theme-color);
  color: #fff;
  padding: 2px;
}

/*mblog*/
.mblog-h2-title {
  background: #f7f7f7 !important;
  padding: 20px 15px 18px !important;
  color: #333 !important;
  border-left: 9px solid #4865b2 !important;
  line-height: 40px !important;
  padding-right: 40px !important;
  padding-left: 40px !important;
}

.mblog-h3-title {
  letter-spacing: 1.6px;
  padding: 0px 15px 0px;
  border-left: 9px solid #4865b2;
  color: #333;
  font-weight: 600;
  margin-top: 60px;
  font-size: 20px;
}

.mblog-h4-title {
  font-weight: 600;
  color: #4663B4;
  letter-spacing: 2.25px;
  padding: 0px 15px 0px;
  border-left: 9px solid #4865b2;
  line-height: 30px;
}

.mblog-pre-text {
  font-size: 17px;
  background-color: #fafafa;
  padding: 40px 25px;
  color: #5a5a5a;
  font-weight: 500;
  line-height: 1.5;
  outline: 1px solid #f0f0f0;
  border-color: #f0f0f0;
  text-align: left;
  margin-left: 40px;
  margin-right: 40px;
  margin-bottom: 40px;
}